import * as React from 'react';

import { IManifestStoreEntry } from './../../../../../../ManifestStore';

import styles from './ManifestListItem.module.scss';

export interface IManifestListItemProps {
  manifest: IManifestStoreEntry;
  selected: boolean;
  clickHandler: () => void;
}

export default class ManifestListItem extends React.Component<IManifestListItemProps, void> {
  public render(): React.ReactElement<IManifestListItemProps> {
    return  <div  className={ this.props.selected ? styles.selectedContainer : styles.container}
                  onClick={this.props.clickHandler}>
                  {this.props.manifest.id}
            </div>;
  }
}
